.enterSite {
    text-align: center;
    background: #000;
    height: 100vh;
    margin: 0;
}

.omen {
    width: 300px;
    height: auto;
    display: block;
    margin: 0px auto;
    opacity: 40%;
    animation: pulse 6s infinite ease;
}

.logo {
    width: 400px;
    height: auto;
    display: block;
    margin: 0px auto;
    opacity: 40%;
    animation: pulse 6s infinite ease;
}

@keyframes pulse{
    0%{
        opacity: 0.1;
    }
    50%{
        opacity: 0.5;
    }
    100%{
        opacity: 0.1;
    }
}

.enterLink {
    color: white;
    text-transform: uppercase;
}

.btn:hover {
    color: darkred;
}