.galleryImage {
    height: 220px;
    width: auto;
    transition: all ease-in-out 0.3s;
    cursor: pointer;
    margin: 30px;
    display: block;
    border: 4px solid white;
    border-radius: 3px;
}

.galleryImage:hover {
    transform: translateY(-10px);
    border: 4px solid darkred;
    box-shadow: 7px 7px 5px 0 rgba(255, 255, 255, 0.2);
}

.gallery {
    background: #000;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}